.container {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 20px;
  grid-template-rows: 50px 1fr 1fr 30px; /* top 50, bottom 100 and divide the rest btween the two other rows */
  box-sizing: border-box;
}

.container div {
  padding: 1px;
}
.header {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
}
.content {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 4;
}

.contact-form {
  border-radius: 20px;
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 4;
  background-color: rgb(248, 249, 250);
  margin: 0px 20px 20px;
}

.footer {
  /* background-color: yellow; */
  margin-top: 50px;
  margin-bottom: 50px;

  grid-column-start: 1;
  grid-column-end: 4;
}
.small-span {
  font-size: 13px;
  color: blue;
  text-transform: uppercase;
}

